        methods: {
            handlePrint() {
                let node = this.$refs.table.cloneNode(true)
                let iframe = document.createElement('iframe')
                iframe.style.display = 'none'
                document.body.appendChild(iframe)
                let style = document.createElement("STYLE")
                let styleContent = document.createTextNode(`
                    @page{size: auto; margin: 20mm;}
                    th,td{border-right: 1px solid #000; border-bottom: 1px solid #000;}
                    thead>tr:nth-child(1)>th{border-top: 1px solid #000;}
                    tbody>tr:last-child>td{border-bottom: 1px solid #000;}
                `)

                style.appendChild(styleContent)
                iframe.contentDocument.head.appendChild(style)
                iframe.contentDocument.body.appendChild(node)
                iframe.contentWindow.print()
                iframe.remove()
            }
        }
    }
</script>

<style scoped>
